<style scoped>
  .type_header {
    width: 100%;
    height: 42px;
    border: 2px solid #f6f6f6;
  }

  .type_con .left .current {
    color: #e93b3d;
    background-color: #fff;
  }

</style>

<style>
  .type_header .left {
    width: 10%;
    line-height: 45px;
    text-align: center;
    float: left;
  }

  .type_header .left img {
    width: 20px;
    margin-top: 12px;
  }

  .type_header .center {
    width: 80%;
    line-height: 40px;
    float: left;
    overflow: hidden;
    position: relative;
  }

  .type_header .center .search_input {
    width: 100%;
    height: 30px;
    border-radius: 15px;
    font-size: 12px;
    box-sizing: border-box;
    padding-left: 45px;
    margin-top: 6px;
    border: none;
    background-color: #f7f7f7;
  }

  .type_header .center i {
    position: absolute;
    width: 30px;
    height: 30px;
    background: url(../assets/images/jd-sprites.png) no-repeat -166px 0px;
    transform: scale(0.5);
    left: 10px;
    top: 7px;
  }

  .type_con {
    width: 100%;
    overflow: auto;
  }

  .type_con .left {
    width: 21%;
    height: calc(100vh - 100px);
    overflow: auto;
    float: left;
    background-color: #fafafa;
  }

  .type_con .left li {
    height: 46px;
    line-height: 46px;
    font-size: 14px;
    color: #333;
    text-align: center;
  }


  .type_con .right {
    width: 79%;
    height: calc(100vh - 100px);
    overflow: auto;
  }

  .type_con .right .right_title {
    width: 92%;
    height: 30px;
    margin: 15px auto;
  }

  .type_con .right h4 {
    font-size: 14px;
    color: #333;
    font-weight: bold;
    float: left;
  }

  .type_con .right .type_b1 {
    width: 12px;
    float: right;
  }

  .type_con .right .type_b2 {
    width: 12px;
    margin-left: 4px;
    float: right;
  }

  .type_con .right span {
    font-size: 10px;
    color: #868686;
    margin-left: 4px;
    float: right;
  }

  .type_con .right .right_con {
    width: 92%;
    margin: 0px auto;
    overflow: auto;
  }

  .type_con .right li {
    width: 33.33%;
    float: left;
    text-align: center;
  }

  .type_con .right img {
    width: 70%;
  }

  .type_con .right .right_con p {
    color: #333;
    font-size: 12px;
    margin-top: 2px;
    margin-bottom: 20px;
  }

</style>



