    margin-left: 10px;
  }

  .daily_con li img {
    width: 70%;
    margin: 5px 0px 10px 10px;
  }

  .recommend {
    width: 100%;
    margin-top: 30px;
  }

  .recommend_list {
    width: 100%;
  }

  .recommend_list li {
    width: 50%;
    float: left;
  }

  .recommend_list li img {
    width: 90%;
    margin-left: 10px;
    margin-bottom: 25px;
  }

  .recommend_list li p {
    margin: 6px 10px;
    font-size: 13px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    /*  */
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
  }

  .recommend_list li b {
    color: #f63030;
    margin-left: 10px;
  }

  .recommend_list li i {
    color: #e4303c;
    font-size: 12px;
    border: 1px solid #e4303c;
    margin-left: 5px;
    padding: 0px 3px;
    font-style: normal;
  }

  .recommend_list li span {
    border: 1px solid #666;
    font-size: 12px;
    color: #666;
    float: right;
    margin-right: 15px;
    margin-top: 2px;
    padding: 0px 5px;
  }

  .recommend_footer {
    margin-bottom: 30px;
  }

  .footer_blank {
    width: 100%;
    height: 60px;
    overflow: hidden;
  }

</style>



